@import url(https://fonts.googleapis.com/css2?family=Kanit:wght@200&display=swap);
header {
    height: 30vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

.header__socials {
    display: flex;
    flex-direction: row;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    bottom: 3rem;
}

.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}


@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }
}

@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }

    .header__socials,
    .scroll__down {
        display: none;
    }
}
nav {
    background: rgba(0, 0, 0, 0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}

nav a {
    background: transparent;
    padding: 0.8rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}

nav a:hover {
    background:rgba(0, 0, 0, 0.3);
}

nav a.active {
    background: var(--color-bg);
    color: var(--color-white)
}
.about__container {
    display:block;
    height: 60vh;
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.about__card {
    background-color: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover {
    background:transparent;
    border-color: var(--color-primary-variant);

    cursor: default;
}

.about__icon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    color: var(--color-white);
    font-size: 0.9rem;
}

.about__card small {
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}

@media screen and (max-width: 1024px) {
    .about__container {
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
    }

    .about__content {
        margin: 1rem 0 1.5rem;
    }
}

@media screen and (max-width: 600px) {
    .about__cards {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
    .about__content {
        text-align: center;
    }
    .about__content p {
        margin: 1.5rem 0;
    }
}
.experience__container {
    display: block;
    grid-gap: 2rem;
    gap: 2rem;
}

.experience__container > div {
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.experience__container > div:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
}

.experience__container > div h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.experience__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2rem;
    row-gap: 2rem;
}

.experience__details {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}

@media screen and (max-width: 1024px) {
    .exeprience__container {
        grid-template-columns: 1fr;
    }
    
    .exeprience__container > div {
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }
}

@media screen and (max-width: 600px) {
    .exeprience__container {
        grid-gap: 1rem;
        gap: 1rem;
    }
}
.pdp__container {
    display: block;
    margin-left: auto;
    margin-right: auto;
}


.app__about {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}


.app__icon {
    margin-left: auto;
    margin-right: auto;
    border-radius: 2rem;
    height: 10rem;
    width: 10rem;
    overflow: hidden;
    box-shadow: 10px 10px 5px var(--color-shadow);
}

.app__screenshots {
    border-radius: 1rem;
    overflow: hidden;
    width: 10rem;
    height: 20rem;
}

.screenshot__image {
    height: auto;
    overflow: hidden;
}


@media screen and (max-width: 1024px) {
    .pdp__container {
        margin-bottom: 2rem;
    }

    .app__icon {
        margin: auto;
    }

    .app__about {
        margin: 1rem 0 1.5rem;
    }
}

@media screen and (max-width: 600px) {

    .pdp__container {
        margin-bottom: 2rem;
    }
    
    .app__about {
        text-align: center;
    }

    .app__icon {
        margin: auto;
    }

    .app__about p {
        margin: 1.5rem 0;
    }
}
.contact__container {
    display: flex;
}

.mail__container {
    margin-left: auto;
    margin-right: auto;
}
* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style-type: none;
    text-decoration: none;
}

:root {
    --color-bg: #465862;
    --color-bg-variant: #90a4ae;
    --color-primary: #78909c;
    --color-primary-variant: rgba(120, 144, 156, 0.4);
    --color-white: #fff;
    --color-light: rgba(215, 225, 230, 0.6);
    --color-shadow: #333a3e;

    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Kanit', sans-serif;
    background: #465862;
    background: var(--color-bg);
    color: #fff;
    color: var(--color-white);
    line-height: 1.7;
}

.container {
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
    font-weight: 500;

}

h1 {
    font-size: 2.5rem;
    text-align: center;
    margin-bottom: 5rem;
}

section {
    margin-top: 8rem;
    height: 75vh;
}

section > h2, section > h5 {
    text-align: center;
    color: rgba(215, 225, 230, 0.6);
    color: var(--color-light);
}

section >h2 {
    color: #78909c;
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: rgba(215, 225, 230, 0.6);
    color: var(--color-light)
}

a {
    color: #78909c;
    color: var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

a:hover {
    color: #fff;
    color: var(--color-white)
}

.btn {
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #78909c;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #78909c;
    border: 1px solid var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition)
}

.btn:hover {
    background: #fff;
    background: var(--color-white);
    color: #465862;
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: #78909c;
    background: var(--color-primary);
    color: #465862;
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}



@media screen and (max-width: 1024px) {
    .container {
        width: 86%;
        width: var(--container-width-md);
    }
    
    section {
        margin-top: 6rem;
    }
}

@media screen and (max-width: 600px) {
    .container {
        width: 90%;
        width: var(--container-width-sm);
    }
    
    section > h2{
        margin-bottom: 2rem;
    }
}
